<template>
  <el-card>
    <el-row class="df aic mb10">
      <el-col :span="2">当前店铺：</el-col>
      <el-col :span="4">
        <el-select v-model="select_store" placeholder="请选择">
          <el-option
            v-for="item in store_name_list"
            :key="item.value"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :span="2" :offset="2">查看月份：</el-col>
      <el-col :span="4">
        <el-date-picker v-model="month_value" type="month" :editable="false" placeholder="选择月"></el-date-picker>
      </el-col>
      <el-col :span="4" :offset="2">
        <el-input v-model="product_name" placeholder="输入商品名称关键词搜索"></el-input>
      </el-col>
      <el-col :span="2" :offset="1">
        <el-button type="primary" class="w22">检索</el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-table :data="brokerage_list" border >
          <el-table-column header-align="center" align="center" prop="order_number" label="销售订单号"></el-table-column>
          <el-table-column
            header-align="center"
            align="center"
            prop="ststem_material_number"
            label="系统物料号"
          ></el-table-column>
          <el-table-column header-align="center" align="center" prop="product_name" label="商品名称"></el-table-column>
          <el-table-column header-align="center" align="center" prop="product_img" label="商品图片">
            <template slot-scope="scope">
              <el-image :src="scope.row.product_img"></el-image>
            </template>
          </el-table-column>
          <el-table-column header-align="center" align="center" prop="order_money" label="订单金额"></el-table-column>
          <el-table-column header-align="center" align="center" prop="brokerage_money" label="返佣金额"></el-table-column>
          <el-table-column header-align="center" align="center" prop="promotions" label="促销员"></el-table-column>
          <el-table-column header-align="center" align="center" prop="order_time" label="订单时间"></el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row class="mt10 df jcfe">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="current_page"
        :page-sizes="[50, 100, 200]"
        :page-size="page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-row>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      store_name_list: [
        { id: 1, name: "蓝拳妈妈" },
        { id: 2, name: "星巴克" },
        { id: 3, name: "瑞星" },
        { id: 4, name: "重庆分公司" }
      ],
      select_store: 1,
      month_value: "2019-11",
      product_name: "",
      brokerage_list: [
        {
          order_number: "121212121",
          ststem_material_number: "23232323232",
          product_name: "iphone13黄色99G网通版",
          product_img:
            "http://offlintab.firefoxchina.cn/static/img/search/baidu_web.png",
          order_money: "￥3333",
          brokerage_money: "￥2222",
          promotions: "库里",
          order_time: "2222/12/12"
        }
      ], //返佣列表
      total: 0, //总共有多少条数据
      current_page: 1, //当前选择的第几页
      page_size: 50 //当前一页选择的条数
    };
  },
  methods: {
    handleSizeChange() {},
    handleCurrentChange() {}
  }
};
</script>

<style scoped >

</style>


